<template>
    <section>
        <div class="">
            <!-- <loader :is-visible="isLoading"></loader> -->
            <div class="row barrw" id="divconclusionheader">
                <div class="col-sm-12 col-md-12 col-lg-12">
                    <div class="heading-border theme-primary partition-full">
                        <h5 class="h5 text-white mt-2 ml-3">
                            <span class="mt-1">{{ $t('Service') }} </span>
                        </h5>
                    </div>
                </div>
            </div>

            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-2">
                <div class="row">
                    <div class="col-md-3">
                        <dynamic-form lang="en" :schema="FormWorkTypeSchema"></dynamic-form>
                    </div>
                </div>
            </div>

            <template>
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                    <div class="row barrw" id="divconclusionheader">
                        <div class="col-sm-12 col-md-12 col-lg-12">
                            <div class="heading-border">
                                <h5 class="h5  mb-3 mt-2">
                                    <span> {{ $t('Service Item') }}</span>
                                </h5>
                            </div>
                        </div>
                    </div>
                    <div>
                        <table id="customers">
                            <tr>
                                <th>Service Name</th>
                                <th>Cost</th>
                                <th>Markup(%)</th>
                                <th>Unit Price</th>
                                <th>Service Duration(HH:MM)</th>
                                <th>Action</th>
                            </tr>
                            <tr>
                                <td><input type="text" id="fname" name="fname" v-model="serviceName"
                                        class="serviceText"></td>
                                <td><input type="text" id="fname" name="fname" v-model="costWithSign"
                                        class="serviceText"></td>
                                <td><input type="text" id="fname" name="fname" v-model="markup" class="serviceText">
                                </td>
                                <td><input type="text" id="fname" name="fname" v-model="unitPriceWithSign"
                                        class="serviceText"></td>
                                <td><input type="text" id="fname" name="fname" v-model="serviceDuration"
                                        class="serviceText"></td>
                                <td><button class="delete-button">
                                        <i class="fa fa-trash"></i>
                                    </button></td>
                            </tr>
                        </table>
                    </div>
                </div>

            </template>

            <template>
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                    <div class="row barrw" id="divconclusionheader">
                        <div class="col-sm-12 col-md-12 col-lg-12">
                            <div class="heading-border">
                                <h5 class="h5  mb-3 mt-2">
                                    <span> {{ $t('Publish Details') }}</span>
                                </h5>
                            </div>
                        </div>
                    </div>
                    <dynamic-form lang="en" :schema="FormSchema"></dynamic-form>
                </div>

            </template>
            <template>
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                    <div class="row barrw availability" id="divconclusionheader">
                        <div class="col-sm-12 col-md-12 col-lg-12">
                            <div class="heading-border">
                                <h5 class="h5  mb-3 mt-2">
                                    <span> {{ $t('Availability') }}</span>
                                </h5>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="container-fluid">
                    <table class="table">
                        <tbody>
                            <tr v-for="(day, index) in weekdays" :key="index">
                                <td>
                                    <div class="circle">{{ index + 1 }}</div>
                                </td>
                                <td>
                                    <input type="checkbox" :id="`weekday-checkbox-${index}`" class="larger-checkbox"
                                        v-model="day.checked" />
                                    <label class="weekCheck-time" :for="`weekday-checkbox-${index}`">{{ day.name
                                        }}</label>
                                </td>
                                <td class="col-md-3">
                                    <input type="checkbox" :id="`twenty-four-checkbox-${index}`" class="larger-checkbox"
                                        v-model="day.twentyFour" />
                                    <label class="weekCheck-time" :for="`twenty-four-checkbox-${index}`">24
                                        Hour</label>
                                </td>
                                <td v-if="day.timePicker">
                                    <div class="row">
                                        <div class="time-picker">
                                            <div class="time-container">
                                                <label :for="`start-time-picker-${index}`">Start:</label>
                                                <input type="time" :id="`start-time-picker-${index}`"
                                                    v-model="day.startTime" @input="updateStartTimeLabel(index)"
                                                    step="1800" class="custom-time-picker" />
                                            </div>
                                        </div>
                                        <div class="time-picker">
                                            <div class="time-container">
                                                <label :for="`end-time-picker-${index}`">End:</label>
                                                <input type="time" :id="`end-time-picker-${index}`"
                                                    v-model="day.endTime" @input="updateEndTimeLabel(index)" step="1800"
                                                    class="custom-time-picker" />
                                            </div>
                                        </div>
                                    </div>
                                </td>
                                <td v-else>
                                    Closed
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>

            </template>
            <template>
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                    <div class="row barrw marketing" id="divconclusionheader">
                        <div class="col-sm-12 col-md-12 col-lg-12">
                            <div class="heading-border">
                                <h5 class="h5  mb-3 mt-2">
                                    <span> {{ $t('Marketing Video') }}</span>
                                </h5>
                            </div>
                        </div>
                    </div>
                    <div class="mb-3 mt-2">
                        <h6>
                            <span class="marketVideo">{{ $t('Having a marketing video that`ll help get people excited '
                                +
                                'about your brand ? Include it here.Example:
                                    (https://www.youtube.com/embed/ly36kn0ug4k)')}}</span>
                        </h6>
                        <h6>{{ $t('Youtube:(You can upload maximum 5 links.)') }}</h6>
                    </div>
                    <div>
                        <div v-for="(textbox, index) in textboxes" :key="index" class="input-container mb-2">
                            <input type="text" class="link-input" v-model="textbox.value">
                            <i class="fa fa-trash delete-icon" v-on:click="removeTextBox(index)" v-if="index !== 0"></i>
                            <i v-else class="fa fa-plus-circle fa-lg add-icon" v-on:click="addNewTextBox()"
                                style="margin-left: 10px;"></i>
                        </div>
                    </div>
                </div>
            </template>
            <template>
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-2">
                    <div class="row barrw description" id="divconclusionheader">
                        <div class="col-sm-12 col-md-12 col-lg-12">
                            <div class="heading-border">
                                <h5 class="h5  mb-3 mt-2">
                                    <span> {{ $t('Description') }}</span>
                                </h5>
                            </div>
                        </div>
                    </div>
                    <div class="mb-3">
                        <dynamic-form lang="en" :schema="FormCkeditorSchema">
                            <template v-slot:tgslot-Description="{ data }">
                                <ckeditor :editor="editor" @ready="onReady">
                                </ckeditor>
                                <span name="Description" class="invalid-feedback d-block">{{ data.error }}</span>
                                <input type="hidden" v-model="data.fieldInfo.value" /> 
                            </template>
                        </dynamic-form>
                    </div>
                </div>
            </template>
            <div class="row flex-row-reverse">
                <div class="col-lg-6 text-right">
                    <a href="javascript:;" class="btn btn-success formbtn widthhalf mr-2" v-on:click="SubmitData()"
                        :title="$t('Save')">
                        <i class="fa fa-save pr-2"></i> {{ $t('Save') }}
                    </a>
                    <a href="javascript:;" class="btn btn-danger formbtn widthhalf" v-on:click="Close()"
                        :title="$t('Cancel')">
                        <i class="fa fa-close pr-2"></i> {{ $t('Cancel') }}
                    </a>

                </div>
                <!-- <div class="col-lg-6">
                                    <div class="py-2">@HtmlHelpers.MandatoryStringFull("R")</div>
                                </div> -->
            </div>
        </div>

    </section>

</template>


<script>
import DataServices from "@/services/DataService";
export default
    {
        data() {
            return {
                editor: this.CLASSIC_EDITOR,
                textboxes: [{ value: '' }],
                serviceName: '',
                cost: '',
                markup: '',
                unitprice: '',
                serviceDuration: '',
                data: '',
                unitPriceWithSign: '',
                costWithSign: '',
                worktype_id: null,
                servicetype_id: null,
                weekdays: [
                    { name: 'Monday', checked: false, twentyFour: false, startTime: '', endTime: '', timePicker: true },
                    { name: 'Tuesday', checked: false, twentyFour: false, startTime: '', endTime: '', timePicker: true },
                    { name: 'Wednesday', checked: false, twentyFour: false, startTime: '', endTime: '', timePicker: true },
                    { name: 'Thursday', checked: false, twentyFour: false, startTime: '', endTime: '', timePicker: true },
                    { name: 'Friday', checked: false, twentyFour: false, startTime: '', endTime: '', timePicker: true },
                    { name: 'Saturday', checked: false, twentyFour: false, timePicker: false },
                    { name: 'Sunday', checked: false, twentyFour: false, timePicker: false }
                ],
                FormWorkTypeSchema: [
                    {
                        layoutType: "single",
                        Data: [
                            {
                                astype: "SelectField",
                                label: this.$t('Work Type'),
                                name: "Work Type",
                                value: "",
                                disabled: true,
                                config: {
                                    options: []
                                },
                                validationRules: { "required": true, max: 100 },
                            },
                        ]
                    },
                ],
                FormSchema: [
                    {
                        layoutType: "four",
                        Data: [
                            {
                                astype: "NumericField",
                                label: this.$t('Service Duration(HH:MM)'),
                                name: "Service Duration",
                                value: "",
                                validationRules: { "required": false, max: 100 },
                            },
                            {
                                astype: "SelectField",
                                label: this.$t('Numbers of booking allowed per slot'),
                                name: "",
                                value: "",
                                config: {
                                    options: [{ value: "1", name: "1" }, { value: "2", name: "2" }, { value: "3", name: "3" }]
                                },
                                validationRules: { "required": false, max: 100 },
                            },
                            {
                                astype: "toggleField",
                                label: this.$t('Raise Request'),
                                name: "Advance Payment",
                                value: "",
                                placeholder: "",
                                validationRules: "",
                            },
                            {
                                astype: "toggleField",
                                label: this.$t('Advance Payment'),
                                name: "Advance Payment",
                                value: "",
                                placeholder: "",
                                validationRules: "",
                            },
                        ],
                    },
                    {
                        layoutType: "single",
                        Data: [
                            {
                                astype: "FileField",
                                showLabel: false,
                                name: "ListAttachment",
                                value: "",
                                placeholder: "",
                                validationRules: "",
                                FieldIndex: 0,
                                config: {
                                    maxSize: 5,
                                    accept: [
                                        {
                                            fileType: 'png',
                                            iconClass: 'fa-file-image-o'
                                        },
                                        {
                                            fileType: 'jpg',
                                            iconClass: 'fa-file-image-o'
                                        },
                                        {
                                            fileType: 'pdf',
                                            iconClass: 'fa-file-archive-o'
                                        }
                                    ],
                                    onChange: this.GetBase64String,
                                    showAddIcon: false,
                                    onAddButtonClick: "",
                                    multiple: true
                                },
                            }
                        ]
                    }
                ],
                FormCkeditorSchema: [
                    {
                        layoutType: "single",
                        Data: [
                            {
                                astype: "SlotField",
                                label: "",
                                name: "Description",
                                value: "",
                                placeholder: "",
                                config: {
                                    options: [],

                                },
                                settings: {
                                    isInSlot: true
                                },
                                visibility: true,
                                validationRules: { "required": false, max: 5000 },
                            },
                        ]
                    },
                ],
            }
        },
        created: async function () {
            var vm = this;
            vm.worktype_id = this.$route.params.item.worktype_id;
            vm.servicetype_id = this.$route.params.item.service_type_id;
            vm.FormWorkTypeSchema[0].Data[0].config.options = this.$route.params.item.worktype_name;
            vm.FormSchema[0].Data[0].value = this.$route.params.item.service_duration;
            vm.serviceName = this.$route.params.item.service_type_name;
            vm.cost = this.$route.params.item.cost;
            vm.markup = this.$route.params.item.markup;
            vm.unitprice = this.$route.params.item.unit_price;
            vm.serviceDuration = this.$route.params.item.service_duration;
            vm.unitPriceWithSign = '$' + vm.unitprice;
            vm.costWithSign = '$' + vm.cost;
            vm.weekdays = JSON.parse(this.$route.params.item.availability_data);
        },
        methods: {
            Close: function () {
                var vueObj = this;
                vueObj.FormSchema[0].Data[0].value = null;
                vueObj.AttachmentfileName = '';
                vueObj.Attachmentfile = null;
                vueObj.AttachmentfileData = null;
                vueObj.FormSchema[0].Data[1].config.onChange = this.GetBase64String;
            },
            addNewTextBox: function () {
                if (this.textboxes.length < 5) {
                    this.textboxes.push({ value: '' });
                }
                else {
                    // Display a message or take any other action to indicate that the maximum limit is reached
                    console.log('Maximum limit reached for adding textboxes.');
                }
            },
            removeTextBox: function (index) {
                var vm = this;
                vm.textboxes.splice(index, 1);
            },
            SubmitData: async function () {
                var vm = this;
                var availabilityData = [];
                vm.weekdays.forEach(function (day, index) {
                    var availabilityObj = {
                        name: day.name,
                        checked: day.checked,
                        twentyFour: day.twentyFour,
                        startTime: day.startTime,
                        endTime: day.endTime,
                        timePicker: day.timePicker
                    };
                    availabilityData.push(availabilityObj);
                });

                var unitPrice = parseFloat(vm.unitPriceWithSign.replace('$', ''));
                var cost = parseFloat(vm.costWithSign.replace('$', ''));

                var postString = {
                    worktype_id: vm.worktype_id,
                    servicetype_id: vm.servicetype_id,
                    servicetype_name: vm.serviceName,
                    cost: cost,
                    markup: vm.markup,
                    unitprice: unitPrice,
                    Service_duration: vm.FormSchema[0].Data[0].value,
                    Bookings: vm.FormSchema[0].Data[3].value,
                    description: vm.FormCkeditorSchema[0].Data[0].value,
                    AvailabilityData: availabilityData,
                    type: 'EDIT'
                };

                var objectPostString = JSON.stringify(postString);
                await DataServices.SavePublishDetail(objectPostString).then(function (response) {
                    if (response.data.result != "" && response.data.result != null) {
                        if (response.data.result.status == 'Success') {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Publish')]), "success", true, vm.$t("Alert"));
                        }
                        else {
                            vm.ShowAlert(vm.$t('ErrorMessage',
                                [vm.$t('Publish')]), "failure", true, vm.$t("Alert"));
                        }
                    }
                });

            },
            GetBase64String: function (evt) {
                try {
                    // debugger;
                    var vm = this;
                    //alert(vm.apiurl);
                    vm.fileData = [];

                    var f = evt.target.files[0]; // FileList object
                    var reader = new FileReader();
                    // Closure to capture the file information.
                    reader.onload = (function (theFile) {
                        debugger;
                        return function (e) {
                            var binaryData = e.target.result;
                            //Converting Binary Data to base 64
                            var base64StringEncoded = window.btoa(binaryData);
                            //showing file converted to base64

                            var fileName = f.name;
                            vm.fileName = fileName;
                            var n = fileName.lastIndexOf(".");
                            var extensionName = fileName.substring(n + 1, fileName.length);

                            vm.fileExt += "." + extensionName;
                            var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                            var fileSize = f.size;
                            vm.fileSize = fileSize;
                            // debugger;
                            // var ggg = vm.configData;
                            var fileObj = {
                                attachement_mime: attachemnetMime,
                                fileName: fileName,
                                id: vm.id,
                                moduleName: vm.modulename,
                                subModuleCode: vm.submodulecode,
                                base64String: base64StringEncoded,
                                fileSize: fileSize
                            };

                            vm.fileData.push(fileObj);
                        };
                    })(f);
                    // Read in the image file as a data URL.
                    reader.readAsBinaryString(f);
                }
                catch (ex) {
                }
            },
        }

    }
</script>


<style>
.design-container {
    display: flex;
    align-items: center;
    margin-left: 13px;
    margin-top: 8px;
}

.circle {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #e2e7ec;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 50px;
}

input[type="checkbox"] {
    margin-right: 15px;
}

.larger-checkbox {
    transform: scale(1.5);
    /* Increase the size of the checkboxes */
}

.weekCheck-time {
    font-size: 16px;
    font-family: inherit;
}

.time-picker {
    display: inline-block;
    width: 200px;
}

.time-container {
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 10px;
    /* Adjust border radius as needed */
}

.time-container label {
    display: block;
    margin-bottom: 5px;
}

.time-container input[type="time"] {
    width: 100%;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 3px;
}

.custom-time-picker {
    height: 40px;
    /* Adjust the height as needed */
}

.availability .marketing .description {
    margin-top: 20px;
}

#customers {
    font-family: Poppins;
    border-collapse: collapse;
    width: 100%;
}

#customers td,
#customers th {
    border: 1px solid #f3eeee;
    padding: 16px;
}


.serviceText {
    border: 1px solid #ece6e6
}

#customers th {
    font-weight: bolder;
    font-size: smaller;
    padding-top: 7px;
    padding-bottom: 9px;
    text-align: left;
    background-color: #f6eeee;
    color: black;
}

.delete-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.delete-button .fa-trash {
    color: red;
    margin-left: 10px;
}

.input-container {
    position: relative;
    margin-bottom: 10px;
}

.link-input {
    border: 1px solid #d3c7c7;
    padding-right: 30px;
    width: 45%;
    margin-bottom: 45px;
    height: 35px;
}

.add-icon {
    cursor: pointer;
    color: #2abe2a;
    font-size: xx-large;
}

.delete-icon {
    background-color: #f80606;
    border-radius: 50%;
    /* Make the button circular */
    border: none;
    cursor: pointer;
    font-size: larger;
    margin-left: 10px;
    border-radius: none;
    cursor: pointer;
    justify-content: center;
    width: 30px;
    /* Set width and height to create a circle */
    height: 30px;
    color: #f6f3f3;
    padding: 5px;
    text-align: center
}
</style>